.hero {
  background-color: #9771E8; /* Background color for the hero section */
  padding: 50px 0; /* Add some padding to the top and bottom */
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 3%;
}

.hero-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px; /* Add some spacing from the edges */
  width: 1600px;
}

.hero-image-container {
  display: flex;
  flex-direction: column; /* Align items in a column */
  align-items: center; /* Center items horizontally */
  position: relative; /* Needed for absolute positioning of floating elements */
  margin-top: 180px;
}

.hero-image {
  position: relative; /* Needed for absolute positioning of the overlay */
  z-index: 1; /* Set a higher z-index for the hero image */
}

.gradient-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url('/public/img/radial.png');
  background-size: cover;
  opacity: 0.6; /* Adjust the opacity as needed */
  z-index: -1; /* Place it above the hero image */
}

.hero-image img {
  animation: floatAnimation 3s ease-in-out infinite; /* Apply the float animation */
  z-index: 3; /* Set a lower z-index for the image to appear below the shadow */
}
/* Define the float animation keyframes */
@keyframes floatAnimation {
  0%, 100% {
      transform: translateY(0); /* Initial and final position */
  }
  50% {
      transform: translateY(-10px); /* Floating position */
  }
}

.image-container {
  display: flex;
  align-items: center; /* Center items vertically */
  justify-content: space-between; /* Space evenly between items */
  padding-right: 15%;
}

/* Style the additional image */
.additional-image img {
  /* Add styles for the fixed image here */
  width: 100px; /* Set the width as needed */
  height: auto; /* Maintain the aspect ratio */
  /* Add any other styles you need */
}

/* Style the shadow element */
.shadow {
  /* ... (Previous shadow styles) ... */
  animation: ombreAnimation 3s ease-in-out infinite;
  display: flex;
  align-items: center;
  height: 20px;
}

@keyframes ombreAnimation {
  0%, 100% {
      transform: scale(1); /* Échelle initiale et finale à 1 (taille normale) */
  }
  50% {
      transform: scale(1.2); /* Échelle augmentée à 1.2 (plus large) */
  }
}

/* Style the hero text */
.hero-text {
  flex: 1; /* Take up available space */
  text-align: left;
  color: #fff; /* Text color is white */
  margin-right: 50px;
  margin-top: 100px;
}

/* Style the hero text heading */
.hero-text h1 {
  font-size: 50px;
  margin: 0;
}

/* Style the subtext */
.subtext {
  font-size: 25px;
  margin: 3px 0; /* Add some spacing between text elements */
}

/* Style the About Me button */
.btn-about-me {
  display: inline-block;
  padding: 12px 22px;
  background-color: #F79D36; /* Button background color */
  color: #fff; /* Button text color is white */
  text-decoration: none;
  font-weight: 300;
  border-radius: 5px; /* Rounded corners */
  margin-top: 50px; /* Add a 50px top margin to move the button down */
  font-size: 20px;
  transition: background-color 0.3s ease, transform 0.3s ease, box-shadow 0.3s ease; /* Added transform transition */
  outline: none; /* Remove default outline */
  border: none; /* Remove any border */
}

.btn-about-me:hover {
  background-color: #ffb662; /* New hover color */
  transform: scale(1.05); /* Slightly increase size on hover */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Add a subtle shadow on hover */
}

.btn-about-me:focus,
.btn-about-me:active {
  outline: none;
  border: none;
}



.welcome-text {
  text-align: center;
  color: #5113A2;
  font-size: 18px;
  margin-bottom: 20px;
  position: relative;
  width: max-content; 
  left: 6%;
}

.welcome-text-h2{
  font-weight: 300;
}

.welcome-text::before {
  content: '';
  position: absolute;
  width: 30px;
  height: 2px;
  background-color: #5113A2;
  top: 50%;
  left: -34px;
  height: 2px;
  top: 26px;
}

/* Scrool button section */
/* Add this to your existing CSS or create a new file (e.g., Hero.css) and import it */

.wrapper {
  height: 100vh;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}

/* Scroll Down Button */
button.scroll_down {
  position: absolute;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  height: 50px;
  width: 30px;
  bottom: 60px;
  background-color: transparent;
  border: 2px solid white;
  border-radius: 20px;
  cursor: pointer;
  outline: none;
}

button.scroll_down:before {
  position: absolute;
  top: 10px;
  left: 50%;
  content: '';
  width: 6px;
  height: 6px;
  margin-left: -3px;
  background-color: #fff;
  border-radius: 100%;
  -webkit-animation: scroll_down_btn-animation 2s infinite;
  animation: scroll_down_btn-animation 2s infinite;
  box-sizing: border-box;
}

@keyframes scroll_down_btn-animation {
  0% {
    transform: translate(0, 0);
    opacity: 0;
  }
  40% {
    opacity: 1;
  }
  80% {
    transform: translate(0, 20px);
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}

@-webkit-keyframes scroll_down_btn-animation {
  0% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0);
    opacity: 0;
  }
  40% {
    opacity: 1;
  }
  80% {
    -webkit-transform: translate(0, 20px);
    transform: translate(0, 20px);
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}
