.back-button {
  position: fixed;
  bottom: 20px; /* Distance from the bottom of the viewport */
  left: 20px; /* Distance from the left side of the viewport */
  background-color: #5113a2;
  color: white;
  border: none;
  border-radius: 50px;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
  z-index: 1000; /* Ensures the button stays above other content, including the footer */
  transition: opacity 0.5s ease, transform 0.5s ease;
  opacity: 0;
  transform: translateY(-20px); /* Start position: slightly above */
}

.next-button {
  position: fixed;
  bottom: 20px; /* Distance from the bottom of the viewport */
  right: 20px; /* Adjust distance from the right side of the viewport */
  background-color: #5113a2;
  color: white;
  border: none;
  border-radius: 50px;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
  z-index: 1000; /* Ensures the button stays above other content, including the footer */
  transition: opacity 0.5s ease, transform 0.5s ease;
  opacity: 0;
  transform: translateY(-20px); /* Start position: slightly above */
}

.back-button.visible,
.next-button.visible {
  opacity: 1;
  transform: translateY(0); /* Normal position when visible */
}

.back-button:hover,
.next-button:hover {
  background-color: #6a29c0;
}
