/* aboutme.css */

/* Styling for the navigation links */
.about-me-nav {
    display: flex;
    justify-content: center;
    margin: 20px 0;
  }
  
  .nav-link {
    margin: 0 15px;
    text-decoration: none;
    color: #333; /* Default color for inactive links */
    font-weight: 500;
  }
  

  
  /* Additional styles for Hero section and other elements */
  .hero_about {
    background-color: #ECB2CA; /* Background color for the hero section */
    padding: 50px 0; /* Add some padding to the top and bottom */
    display: flex;
    justify-content: center; /* Center content horizontally */
    align-items: center;
    margin-bottom: 3%;
    min-height: 80vh; /* Adjusted to min-height to prevent fixed height issues */
    width: 100%; /* Ensure full width of the hero section */
  }
  
  .hero-content_about {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1200px; /* Set max width for the hero content */
    width: 100%; /* Allow it to scale within its max-width */
    margin: 0 auto; /* Center the content horizontally */
    padding: 0 15px; /* Add some spacing from the edges */
  }
  
  .hero-text_about {
    flex: 1; /* Take up equal space with the image container */
    text-align: left;
    color: #3B247C; /* Text color is white */
    margin-right: 20px;
    z-index: 1;
    max-width: 50%; /* Ensure text takes up 50% of the container */
  }
  
  .hero-image-container_about {
    flex: 1; /* Take up equal space with the text container */
    display: flex;
    flex-direction: column;
    align-items: center; /* Center items horizontally */
    position: relative;
    max-width: 50%; /* Ensure image takes up 50% of the container */
  }
  
  .hero-image_about {
    position: relative;
    width: 100%;
    transform: translateX(-30px) scale(1.3);
  }
  
  .hero-image_about img {
    width: 100%; /* Ensuring the image scales properly within its container */
    height: auto; /* Maintain aspect ratio */
    border-radius: 10px; /* Adjust if you want rounded corners */
  }
  
  .welcome-text_about {
    text-align: center;
    color: #5113A2;
    font-size: 18px;
    margin-bottom: 20px;
    position: relative;
    width: max-content; 
    left: 37px; /* Center alignment adjustment */
    margin-top: 0px;
    margin-right: auto;
    margin-bottom: 0px;

  }
  
  .welcome-text-h2_about {
    font-weight: 300;
  }
  
  .welcome-text_about::before {
    content: '';
    position: absolute;
    width: 30px;
    height: 2px;
    background-color: #5113A2;
    top: 50%;
    left: -34px;
    top: 26px;
  }
  
  .title_about {
    font-size: 2.5em;
    margin: 10px 0; /* Add some margin for spacing */
  }
  
  .btn-about-me_2 {
    background-color: #5113A2; /* Button color */
    display: inline-block;
    padding: 12px 22px;
    
    color: #fff; /* Button text color is white */
    text-decoration: none;
    font-weight: 300;
    border-radius: 5px; /* Rounded corners */
    margin-top: 50px; /* Add a 50px top margin to move the button down */
    font-size: 20px;
    transition: background-color 0.5s ease;
  }
  
  .btn-about-me_2:hover {
    background-color: #6a29c0; /* Hover effect color */
  }
  
  /* Scroll Down Button */
  .wrapper2 {
    height: 100vh;
    text-align: center;
  }
  

  .about-block {
    background-color: #5113A2; /* Background color of the block */
    padding: 40px; /* Padding for the block */
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 40px auto; /* Margin to center the block and add space around */
    max-width: 1080px; /* Maximum width for the block */
    border-radius: 10px; /* Rounded corners */
  }
  
  .about-block-content {
    display: flex;
    align-items: center;
    width: 100%; /* Full width for internal content */
  }
  
  .about-image {
    flex: 0 0 20%; /* Reduce the image width to 20% of the block */
    padding-right: 40px; /* Increase space between image and text */
  }
  
  .about-image img {
    width: 100%; /* Image should fill its container */
    border-radius: 10px; /* Rounded corners for the image */
  }
  
  .about-text {
    flex: 1; /* Take up the remaining space for the text */
    color: white; /* Text color */
  }
  
  .about-text h2 {
    font-size: 28px;
    margin-bottom: 20px;
  }
  
  .about-text p {
    font-size: 16px;
    line-height: 1.6;
  }
  